import Vue from 'vue'
import Router from 'vue-router'
import corpAuth from '@/components/corpAuth'
import constant from '../utils/constant'
import config from '../common'
import store from '@/store'

// index
import indexWrap from '@/page/index/indexWrap'
import home from '@/page/index/home' //

// center
import centerWrap from '@/page/center/centerWrap'
import centerFull from '@/page/center/centerFull'
import center from '@/page/center/center' //
import partnerWrap from '@/page/center/365partner/partnerWrap'
import code from '@/page/center/365partner/code' //365
import apply from '@/page/center/365partner/apply' //365
import inviteList from '@/page/center/365partner/inviteList' //365
import inviteRelu from '@/page/center/365partner/inviteRelu' //365
import applyStatic from '@/page/center/365partner/applyStatic' //365

//user
import userWrap from '@/page/user/userWrap'
import login from '@/page/user/login' //
import find_password from '@/page/user/find_password' //
import password_change from '@/page/user/password_change' //
import phone_change from '@/page/user/phone_change' //
import register_password from '@/page/user/register_password' //
import phone_bind from '@/page/user/phone_bind' //

const base = config.path

console.log(`base : ${base}`)

Vue.use(Router)

let router = new Router({
  mode: 'history',
  base,
  routes: [{
      path: '/',
      redirect: '/center'
    },
    {
      path: '/index',
      name: 'index',
      component: indexWrap,
      children: [{
        path: '/',
        component: home
      }],
      meta: {
        title: ''
      }
    },
    {
      path: '/center',
      name: 'center',
      component: centerWrap,
      children: [{
        path: '',
        component: center
      }],
      meta: {
        title: ''
      }
    },
    {
      path: '/centerFull',
      name: 'centerFull',
      component: centerFull,
      children: [{
        path: 'partner',
        component: partnerWrap,
        children: [{
          path: '',
          redirect: 'code'
        }, {
          path: 'code',
          name: 'code',
          component: code,
          meta: {
            title: '365'
          }
        }, {
          path: 'apply',
          name: 'apply',
          component: apply,
          meta: {
            title: ''
          }
        }, {
          path: 'applyStatic',
          name: 'applyStatic',
          component: applyStatic,
          meta: {
            title: '365'
          }
        },{
          path: 'inviteRelu',
          name: 'inviteRelu',
          component: inviteRelu,
          meta: {
            title: ''
          }
        },{
          path: 'inviteList',
          name: 'inviteList',
          component: inviteList,
          meta: {
            title: ''
          }
        }]
      }]
    },
    {
      path: '/user',
      name: 'user',
      component: userWrap,
      children: [{
          path: '',
          redirect: 'login'
        },
        {
          path: 'corpAuth',
          name: 'corpAuth',
          component: corpAuth,
          meta: {
            title: ''
          }
        },
        {
          path: 'login',
          name: 'login',
          component: login,
          meta: {
            title: '/'
          }
        },
        {
          path: 'find_password',
          name: 'find_password',
          component: find_password,
          meta: {
            title: ''
          }
        },
        {
          path: 'password_change',
          name: 'password_change',
          component: password_change,
          meta: {
            title: ''
          }
        },
        {
          path: 'phone_change',
          name: 'phone_change',
          component: phone_change,
          meta: {
            title: ''
          }
        },
        {
          path: 'phone_bind',
          name: 'phone_bind',
          component: phone_bind,
          meta: {
            title: ''
          }
        },
        {
          path: 'register_password',
          name: 'register_password',
          component: register_password,
          meta: {
            title: ''
          }
        }
      ],
      meta: {
        title: ''
      }
    },
    {
      path: '*',
      redirect: '/'
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})

router.beforeEach((to, from, next) => {
  console.log(`----------- from Router --------------`)
  console.log(from)
  console.log(`----------- to   Router --------------`)
  console.log(to)
  console.log(`----------- End  Router --------------`)

  if (!store.state.comm.indexUrl) {
    store.commit('updateUrl', window.location.href)
    next()
  } else {
    next()
  }
  // if (to.name == 'corpAuth') {
  //   next();
  //   return;
  // }
  // let authorization = localStorage.getItem(constant.AUTHORIZATION_KEY)
  // let payload = localStorage.getItem(constant.PAYLOAD_KEY)

  // console.log('  ')
  // console.log(`authorization : ${authorization} && payload : ${payload}`)

  // if (!authorization) {
  //   console.log(``)
  //   next({
  //     path: '/corpAuth'
  //   })
  // } else {
  //   console.log('', to)
  //   next()
  // }
})

export default router



